
body{margin:0;background-color:#e1e1e1
}
img{vertical-align:initial
}
.my-message{width:90%;margin:10px auto
}
@media only screen and (max-width: 950px){
.my-message .lift-content{z-index:16;width:100% !important
}
.my-message .right-content{visibility:hidden !important;width:0px !important
}
}
.my-message .lift-content{float:left;width:calc(100% - 300px)
}
.my-message .lift-content .box-card{margin-bottom:10px
}
.my-message .lift-content .box-card .el-card__header{padding:8px 20px;padding-bottom:0
}
.my-message .lift-content .box-card .el-card__header .card-title{height:40px;line-height:40px;font-size:14px
}
.my-message .lift-content .box-card .el-card__header .card-title .all-read{margin-top:2px;float:right
}
.my-message .lift-content .box-card .el-card__body{padding:0
}
.my-message .lift-content .box-card .el-card__body .notread-item,.my-message .lift-content .box-card .el-card__body .read-item{padding:10px 20px;border-bottom:1px solid #f0f0f0
}
.my-message .lift-content .box-card .el-card__body .notread-item .user-img,.my-message .lift-content .box-card .el-card__body .read-item .user-img{margin-right:10px;float:left
}
.my-message .lift-content .box-card .el-card__body .notread-item .read-messages-title,.my-message .lift-content .box-card .el-card__body .read-item .read-messages-title{float:left
}
.my-message .lift-content .box-card .el-card__body .notread-item .read-messages-title .title,.my-message .lift-content .box-card .el-card__body .read-item .read-messages-title .title{font-size:18px;font-weight:bold
}
.my-message .lift-content .box-card .el-card__body .notread-item .read-messages-title .desc,.my-message .lift-content .box-card .el-card__body .read-item .read-messages-title .desc{font-size:14px;color:#b4b4b4
}
.my-message .lift-content .box-card .el-card__body .notread-item .read-btn,.my-message .lift-content .box-card .el-card__body .read-item .read-btn{margin-top:6px;float:right
}
.my-message .lift-content .box-card .el-card__body .notread-item .reply-content,.my-message .lift-content .box-card .el-card__body .read-item .reply-content{clear:both
}
.my-message .lift-content .box-card .el-card__body .notread-item .topic-title,.my-message .lift-content .box-card .el-card__body .read-item .topic-title{background-color:#b4b4b4;padding:4px
}
.my-message .lift-content .box-card .el-card__body .notread-item .user-img{width:40px
}
.my-message .lift-content .box-card .el-card__body .notread-item .read-messages-title{width:calc(100% - 60px - 50px)
}
.my-message .lift-content .box-card .el-card__body .notread-item .read-btn{width:60px
}
.my-message .lift-content .box-card .el-card__body .read-item .user-img{width:40px
}
.my-message .lift-content .box-card .el-card__body .read-item .read-messages-title{width:calc(100% - 50px)
}
.my-message .lift-content .box-card .el-card__body .is-notdata{font-size:12px;margin:20px
}
.my-message .right-content{float:right;width:290px
}
.my-message .right-content .box-card{margin-bottom:10px
}
.my-message .right-content .box-card .not-login .describe{margin-top:20px;font-size:14px
}
.my-message .right-content .box-card .not-login .describe .not-bottom{display:inline-block;height:20px;line-height:20px
}
.my-message .right-content .box-card .not-login .describe .el-link{margin-top:-3px
}
.my-message .right-content .box-card .el-card__header{padding:8px 20px;padding-bottom:0
}
.my-message .right-content .box-card .el-card__header .card-title{height:40px;line-height:40px;font-size:14px
}
.my-message .right-content .box-card .user{height:60px
}
.my-message .right-content .box-card .user .user-img{width:40px;margin-right:10px;float:left
}
.my-message .right-content .box-card .user .user-name{width:calc(100% - 50px);font-size:18px;float:left
}
.my-message .right-content .box-card .client-box{width:100%
}
.my-message .right-content .box-card .client-box .el-link{display:inline-block;width:100%;text-align:center
}
